<script setup lang="ts">
import { computed } from "vue";
import { useI18n } from "vue-i18n";
import { ACol, ASelect, ACheckbox } from "@actindo/core-ui";
import type { IDataTypeInstance } from "@/services/api/DataHub/Attributes";
import type { ITenantAttributeLanguage } from "@/services/api/DataHub/Languages";
import type { ITenantAttributeScope } from "@/services/api/DataHub/Scopes";
import type { IDataTypesDateTimeValueConfiguration } from "./models";
import { DATE_FORMATS, TIME_FORMATS } from "../constants";

defineProps<{
  dataTypeInstance: IDataTypeInstance<IDataTypesDateTimeValueConfiguration>;
  remoteScopes?: ITenantAttributeScope[];
  remoteLanguages?: ITenantAttributeLanguage[];
  readonly?: boolean;
}>();

const configuration = defineModel<IDataTypesDateTimeValueConfiguration>();

const { t } = useI18n();

const formatToItem = (formats: Record<string, string>) =>
  Object.entries(formats).map(([value, title]) => ({ value, title: t(title) }));

const dateFormats = computed(() => formatToItem(DATE_FORMATS));
const timeFormats = computed(() => formatToItem(TIME_FORMATS));
</script>

<template>
  <ACol>
    <ASelect
      v-model="configuration.dateFormat"
      :items="dateFormats"
      :label="$t('dateTime.dateFormat')"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ASelect
      v-model="configuration.timeFormat"
      :items="timeFormats"
      :label="$t('dateTime.timeFormat')"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ACheckbox
      v-model="configuration.readOnly"
      :label="$t('stringValue.readOnly')"
      :readonly="readonly"
    />
  </ACol>
</template>
